import React from 'react'
import axios from 'axios'
import {config} from './Constants'


class Login extends React.Component{
  state = {
    email: '',
    password: ''
  }

  //handle typing in the form
  handleInput = (ev) => {
      switch(ev.target.name){
        case 'email':
          this.setState({email: ev.target.value})
          break;
        case 'password':
          this.setState({password: ev.target.value})
      }
    } //handleInput //handleInput

  //handle the submit of the login
  handleSubmit = (ev) => {
   //create a request object we can pass through to knock
   const request = {'email': this.state.email, 'password': this.state.password}

   //do an axios post request where we can send through the user details to rails and login
   axios.post(`${config.url.API_URL}/user_token`, {auth: request})
   .then(result => {
     localStorage.setItem("jwt", result.data.jwt)
     this.props.setCurrentUser();
     this.props.history.push('/my_profile');
   })
   .catch(err => {
     console.warn(err)
   })
   ev.preventDefault();
 }

 render(){
   return(
     <form onSubmit={this.handleSubmit}>
       <label>Login Form</label>
       <br/>
       <input
         onChange={this.handleInput}
         name="email"
         type="email"
         placeholder='Enter Email'
       />
       <br/>
       <input
         onChange={this.handleInput}
         name="password"
         type="password"
         placeholder='Enter Password'
       />
       <br/>
       <button>Login</button>
     </form>

   ); // return
 }// render
} // class Login

export default Login
